import { AnimatePresence, motion } from 'framer-motion';
import { forwardRef, useRef, useState } from 'react';
import styles from './uiInputText.module.scss';
import { UILabel } from './uiLabel';

const UIInputTextComponent = forwardRef(({ children, ...props }, ref) => {
  const elementLabel = useRef(null);
  const elementIndicator = useRef(null);

  const [isFocused, setFocused] = useState(false);

  return (
    <motion.label
      className={styles.root}
      ref={elementLabel}
      htmlFor={props.id}
      onFocus={() => setFocused(true)}
      onBlur={() => setFocused(false)}>
      <input {...props} className={styles.rootInput} ref={ref} autofill="off" />
      <UILabel className={styles.rootLabel}>{children}</UILabel>

      <AnimatePresence>
        {isFocused && (
          <motion.span
            initial={{ scaleX: 0 }}
            animate={{ scaleX: 1 }}
            exit={{ scaleX: 0 }}
            transition={{ ease: 'circOut', duration: 0.2 }}
            className={styles.rootIndicator}
            ref={elementIndicator}></motion.span>
        )}
      </AnimatePresence>
    </motion.label>
  );
});

export const UIInputText = motion(UIInputTextComponent);
