import { motion } from 'framer-motion';
import { forwardRef } from 'react';
import styles from './uiButton.module.scss';

const UIButtonComponent = forwardRef(({ children, ...props }, ref) => {
  return (
    <motion.button className={styles['button']} ref={ref} {...props}>
      {children}
    </motion.button>
  );
});

export const UIButton = motion(UIButtonComponent);
