import { motion } from 'framer-motion';
import { forwardRef } from 'react';
import { EmailTemplate } from './email/emailTemplate';

const AppBodyTemplateComponent = forwardRef(({ defaultUserDetails, getUserDetails, children, ...props }, ref) => (
  <motion.div ref={ref} {...props}>
    <EmailTemplate {...{ defaultUserDetails }} {...{ getUserDetails }} />
    {children}
  </motion.div>
));

export const AppBodyTemplate = motion(AppBodyTemplateComponent);
