import { useEffect, useState, useRef } from 'react';
import { style } from './emailTemplateStyles.jsx';

export const HelperRowSpace = ({ space }) => {
  return (
    <tr>
      <td>
        <TemplateHTMLTableElement style={{ height: space }}>
          <tbody>
            <tr>
              <td style={{ height: space, lineHeight: `${space}px` }}>
                <br />
              </td>
            </tr>
          </tbody>
        </TemplateHTMLTableElement>
      </td>
    </tr>
  );
};
export const HelperColSpace = ({ space }) => {
  return (
    <td>
      <TemplateHTMLTableElement style={{ width: space }}>
        <tbody>
          <tr>
            <td style={{ width: space, lineHeight: `${space}px` }}>
              <br />
            </td>
          </tr>
        </tbody>
      </TemplateHTMLTableElement>
    </td>
  );
};

// ----------------------------------------------------------------------------

const styleHTMLTableElement = {
  ...style,
  borderCollapse: 'collapse',
  border: null,
  lineHeight: null,
  padding: null,
};
const propsHTMLTableElement = {
  cellSpacing: 0,
  cellPadding: 0,
  border: 0,
  height: 'auto',
  role: 'presentation',
  width: '100%',
};

export const TemplateHTMLTableElement = ({ children, style: inheritedStyle, ...props }) => {
  return (
    <table
      {...{ ...propsHTMLTableElement, ...props }}
      style={{
        ...{
          ...styleHTMLTableElement,
          height: props.height || styleHTMLTableElement.height,
          width: props.width || styleHTMLTableElement.width,
        },
        ...inheritedStyle,
      }}>
      {children}
    </table>
  );
};

// ----------------------------------------------------------------------------

const styleHTMLAnchorElement = {
  ...style,
  display: 'block',
};
const propsHTMLAnchorElement = {
  rel: 'noopener noreferrer',
};

export const TemplateHTMLAnchorElement = ({ children, style: inheritedStyle, ...props }) => {
  return (
    <a {...{ ...propsHTMLAnchorElement, ...props }} style={{ ...styleHTMLAnchorElement, ...inheritedStyle }}>
      <span style={{ ...styleHTMLAnchorElement, ...inheritedStyle }}>{children}</span>
    </a>
  );
};

// ----------------------------------------------------------------------------

const styleHTMLImageElement = {
  ...style,
  fontFamily: null,
  fontWeight: null,
  height: 'auto',
  lineHeight: '100%',
  outline: 'none',
  width: 'auto',
};
const propsHTMLImageElement = {
  alt: '',
  border: 0,
};

export const TemplateHTMLImageElement = ({ children, style: inheritedStyle, ...props }) => {
  const [imageLoading, setImageLoading] = useState(true);
  const imageRef = useRef();

  useEffect(() => {
    setImageLoading(true);
  }, [props.src]);

  return (
    <img
      ref={imageRef}
      alt={props.alt}
      {...{ ...propsHTMLImageElement, ...props }}
      onLoad={() => setImageLoading(false)}
      style={{
        ...{
          ...styleHTMLImageElement,
          height: props.height || styleHTMLImageElement.height,
          width: imageLoading ? 'auto' : imageRef.current.width || props.width || styleHTMLImageElement.width,
        },
        ...inheritedStyle,
      }}
    />
  );
};
