import { motion } from 'framer-motion';
import { forwardRef, useContext } from 'react';
import { ClientContext } from '../context/contextClient';
import styles from './appFooter.module.scss';

const AppFooterComponent = forwardRef((props, ref) => {
  const clientContext = useContext(ClientContext);
  return (
    <motion.footer ref={ref} className={styles['app__footer']}>
      <small>Developed for {clientContext.name} by Shay Fletcher Inc.</small>
    </motion.footer>
  );
});

export const AppFooter = motion(AppFooterComponent);
