import { motion } from 'framer-motion';
import { forwardRef, useEffect, useRef, useState } from 'react';
import { UIInputText } from './ui/uiInputText';

const AppBodySettingsComponent = forwardRef(({ getUserDetails, setUserDetails, ...props }, ref) => {
  const optionNameFirst = useRef(null);
  const optionNameFamily = useRef(null);
  const optionRole = useRef(null);
  const optionPhoneLegacy = useRef(null);
  const optionPhoneMobile = useRef(null);
  const optionEmail = useRef(null);
  const optionAddressOffice = useRef(null);
  const optionAddressWorkshop = useRef(null);

  const childAnimationVariants = {
    leave: { x: -100, opacity: 0 },
    enter: { x: 0, opacity: 1 },
  };

  const [getLegacyPhoneState, setLegacyPhoneState] = useState('');
  const [getMobilePhoneState, setMobilePhoneState] = useState('');

  useEffect(() => {
    setUserDetails(prevUserDetails => ({
      ...prevUserDetails,
      phoneLegacy:
        getLegacyPhoneState &&
        '+61 ' + [...getLegacyPhoneState].map((digit, index) => (index !== 0 || digit !== '0' ? digit : '')).join(''),
    }));
  }, [getLegacyPhoneState, setUserDetails]);

  useEffect(() => {
    setUserDetails(prevUserDetails => ({
      ...prevUserDetails,
      phoneMobile:
        getMobilePhoneState &&
        '+61 ' + [...getMobilePhoneState].map((digit, index) => (index !== 0 || digit !== '0' ? digit : '')).join(''),
    }));
  }, [getMobilePhoneState, setUserDetails]);

  return (
    <motion.div {...props} ref={ref}>
      <UIInputText
        variants={childAnimationVariants}
        ref={optionNameFirst}
        id={'nameFirst'}
        autoComplete="given-name"
        onChange={() =>
          setUserDetails(prevUserDetails => ({ ...prevUserDetails, nameFirst: optionNameFirst.current.value }))
        }>
        First Name
      </UIInputText>
      <UIInputText
        variants={childAnimationVariants}
        ref={optionNameFamily}
        id={'nameFamily'}
        autoComplete="family-name"
        onChange={() =>
          setUserDetails(prevUserDetails => ({ ...prevUserDetails, nameFamily: optionNameFamily.current.value }))
        }>
        Last Name
      </UIInputText>
      <UIInputText
        variants={childAnimationVariants}
        ref={optionRole}
        id={'role'}
        autoComplete="organization-title"
        onChange={() => setUserDetails(prevUserDetails => ({ ...prevUserDetails, role: optionRole.current.value }))}>
        Role
      </UIInputText>
      <UIInputText
        variants={childAnimationVariants}
        ref={optionPhoneLegacy}
        id={'phoneLegacy'}
        type="tel"
        value={getLegacyPhoneState}
        autoComplete="on"
        onChange={() => setLegacyPhoneState(optionPhoneLegacy.current.value.replace(/[^0-9\s]+/g, ''))}>
        Phone Number
      </UIInputText>
      <UIInputText
        variants={childAnimationVariants}
        ref={optionPhoneMobile}
        id={'phoneMobile'}
        type="tel"
        value={getMobilePhoneState}
        autoComplete="on"
        onChange={() => setMobilePhoneState(optionPhoneMobile.current.value.replace(/[^0-9\s]+/g, ''))}>
        Mobile Number
      </UIInputText>
      <UIInputText
        variants={childAnimationVariants}
        ref={optionEmail}
        id={'email'}
        type="url"
        autoComplete="email"
        onChange={() => setUserDetails(prevUserDetails => ({ ...prevUserDetails, email: optionEmail.current.value }))}>
        Email Address
      </UIInputText>
      <UIInputText
        variants={childAnimationVariants}
        ref={optionAddressOffice}
        id={'addressOffice'}
        type="text"
        autoComplete="address-line1"
        onChange={() =>
          setUserDetails(prevUserDetails => ({ ...prevUserDetails, addressOffice: optionAddressOffice.current.value }))
        }>
        Office Address
      </UIInputText>
      <UIInputText
        variants={childAnimationVariants}
        ref={optionAddressWorkshop}
        id={'addressWorkshop'}
        type="text"
        autoComplete="address-line1"
        onChange={() =>
          setUserDetails(prevUserDetails => ({
            ...prevUserDetails,
            addressWorkshop: optionAddressWorkshop.current.value,
          }))
        }>
        Workshop Address
      </UIInputText>
    </motion.div>
  );
});

export const AppBodySettings = motion(AppBodySettingsComponent);
