import classNames from 'classnames';
import { motion } from 'framer-motion';
import { forwardRef } from 'react';
import styles from './uiLabel.module.scss';

const UILabelComponent = forwardRef(({ children, ...props }, ref) => {
  return (
    <motion.span
      initial={{ opacity: 0 }}
      animate={{ opacity: 1 }}
      transition={{ delay: 0.75 }}
      className={classNames(props.className, styles.root)}>
      {children}
    </motion.span>
  );
});

export const UILabel = motion(UILabelComponent);
