import 'clipboard-polyfill/overwrite-globals';
import { motion } from 'framer-motion';
import { forwardRef, useContext, useMemo, useRef, useState } from 'react';
import { ClientContext } from '../context/contextClient';
import { handleCopy } from '../helpers/handleCopy';
import styles from './appBody.module.scss';
import { AppBodySettings } from './appBodySettings';
import { AppBodyTemplate } from './appBodyTemplate';
import { UIButton } from './ui/uiButton';

const AppBodyComponent = forwardRef((props, ref) => {
  const clientContext = useContext(ClientContext);

  const appBodyTemplateRef = useRef(null);
  const appBodySettingsRef = useRef(null);

  const defaultUserDetails = useMemo(
    () => ({
      ...clientContext,
      contactMessage: '',
      nameFirst: 'Name',
      nameFamily: 'Surname',
      role: 'Your Role',
    }),
    [clientContext],
  );

  const [getUserDetails, setUserDetails] = useState(defaultUserDetails);

  const parentAnimationVariants = {
    leave: { x: 0, opacity: 0 },
    enter: { x: 0, opacity: 1 },
  };

  return (
    <motion.section ref={ref} className={styles['app__body']}>
      <AppBodySettings
        ref={appBodySettingsRef}
        setUserDetails={setUserDetails}
        getUserDetails={getUserDetails}
        className={styles['app__body-settings']}
        variants={parentAnimationVariants}
        initial={'leave'}
        animate={'enter'}
        transition={{ delay: 0, staggerChildren: 0.05 }}
      />
      <AppBodyTemplate
        ref={appBodyTemplateRef}
        defaultUserDetails={defaultUserDetails}
        getUserDetails={getUserDetails}
        className={styles['app__body-template']}
        variants={parentAnimationVariants}
        initial={'leave'}
        animate={'enter'}
        transition={{ delay: 0.5, staggerChildren: 0.05 }}>
        <UIButton
          variants={parentAnimationVariants}
          initial={'leave'}
          animate={'enter'}
          transition={{ delay: 0.5 }}
          onClick={evt => handleCopy(evt.target, appBodyTemplateRef.current)}>
          Copy to Clipboard
        </UIButton>
      </AppBodyTemplate>
    </motion.section>
  );
});

export const AppBody = motion(AppBodyComponent);
