const handleCopy = (targetButton, targetContents) => {
  console.log(navigator.clipboard);
  const copyContents = new window.ClipboardItem({
    'text/html': new Blob([targetContents.firstElementChild.outerHTML], { type: 'text/html' }),
  });
  console.log(copyContents);
  navigator.clipboard.write([copyContents]).then(async () => {
    targetButton.textContent = 'Copied!';
    await new Promise(resolve => setTimeout(resolve, 1000));
    targetButton.textContent = 'Copy to Clipboard';
  });
};

export { handleCopy };
